@import url('https://fonts.googleapis.com/css2?family=Roboto:ital,wght@0,500;1,400&display=swap');

* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

html {
    font-size: 62.5%;
    font-family: 'Roboto', sans-serif;
}

li {
    list-style: none;
}

a {
    text-decoration: none;
}
.header{
  border-bottom: 1px solid #7abc4b;
  background-color: #000;
  cursor: pointer;
}

.navbar {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 1rem 1.5rem;
}

.hamburger {
  display: none;
}

.bar {
  display: block;
  width: 25px;
  height: 3px;
  margin: 5px auto;
  -webkit-transition: all 0.3s ease-in-out;
  transition: all 0.3s ease-in-out;
  background-color: #101010;
}

.nav-menu {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-right: 40px;
}

.nav-item {
  margin-left: 5rem;
}

.nav-link{
  font-size: 1.6rem;
  font-weight: 400;
  color: #7abc4b;
}

.nav-link:hover{
  color: #482ff7;
}

.nav-logo {
  font-size: 2.1rem;
  font-weight: 500;
  color: #482ff7;
}


.main-container{
  width: 100%;
  height: 100%;
  max-width: 100vw;
  max-height: 100vh;
  background-color: #fff;
}

.nav-logo{
  width: 40px;
  height: 40px;
  margin: 5px 10px;
}

.hero-image-container{
  background: url('../images//hero-image.png') center / cover;
  position: relative;
  height: 60vh;
  width: 100%;
}
.hero-image-find-container{
  background: url('../images//hero-find-image.jpg') center / cover;
  position: relative;
  height: 60vh;
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
}
.hero-image-find-container p{
  color: white;
  font-size: 52px;
  position: relative;
}
.hero-image-find-container::before {
  content: " ";
  background: rgb(37, 121, 23);
  display: block;
  position: absolute;
  width: 100%;
  height: 100%;
  opacity: 0.4;
}
.hero-text {
  padding: 40px;
  position: absolute;
  bottom: 40px;
  left: 14%;
  width: 100%;
  height: auto;
  opacity: 0;
  animation: slidein infinite 20s;
  z-index: 10;
}
.portal-image{
  width: 155px;
  height: 155px;
  position: absolute;
  right: calc(100% - 150px);
  bottom: 50px;
  animation: reveal infinite 20s;
  z-index: 5;
}
.hero-text h1{
  color: white;
  font-size: 32px;
  margin-bottom: 10px;
}
.hero-text h2{
  color: white;
  font-size: 24px;
  margin-bottom: 5px;
}
.hero-text h3{
  color: white;
  font-size: 20px;
}
.hero-image-container::before {
  content: " ";
  background: rgb(41, 41, 41);
  display: block;
  position: absolute;
  width: 100%;
  height: 100%;
  opacity: 0.4;
}
.characters-container{
  width: 100%;
  padding: 75px 30px 150px 30px;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-around;
  align-items: center;
  position: relative;
}
/* Home styles */
.title-container{
  width: 100%;
  background-color: rgb(230, 233, 237);
  height: 150px;
  text-align: center;
  display: flex;
  justify-content:center;
  align-items: center;
  font-size: 22px;
}

.load-more-button{
  position: absolute;
  bottom: 70px;
  left: calc(50% - 85px);
  width: 155px;
  height: 45px;
  background-color: #000;
  color: #7abc4b;
  border-radius: 40px;
  font-size: 18px;
  font-weight: 500;
  cursor: pointer;
  box-shadow: 0 4px 8px 0 rgba(0,0,0,0.2);
}

/* Card styles */
.flip-card {
  background-color: transparent;
  width: 25%;
  height: 340px;
  perspective: 1000px;
  margin: 20px;
  border-radius: 8px;
}
.flip-card h1{
  font-size: 18px;
  margin-bottom: 5px;
}
.flip-card p{
  font-size: 14px;
  margin-bottom: 5px;
}
.flip-card-image{
  width: 100%;
  height: 240px;
  padding: 0;
  margin: 0;
}
.flip-card-image img{
  width: 100%;
  height: 100%;
}
.flip-card-basic-data{
  width: 100%;
  height: 100px;
  background-color: #fff;
}
.flip-card-inner {
  position: relative;
  width: 100%;
  height: 100%;
  text-align: center;
  transition: transform 0.8s;
  transform-style: preserve-3d;
  box-shadow: 0 4px 8px 0 rgba(0,0,0,0.2);
}

.flip-card:hover .flip-card-inner {
  transform: rotateY(180deg);
  border: none;
}

.flip-card-front, .flip-card-back {
  position: absolute;
  width: 100%;
  height: 100%;
  -webkit-backface-visibility: hidden;
  backface-visibility: hidden;
}

.flip-card-front {
  background-color: #bbb;
  color: black;
}

.flip-card-back {
  background-color: black;
  color: #7abc4b;
  transform: rotateY(180deg);
}
.flip-card-data h1{
  margin-bottom: 10px;
  font-size: 18px;
}
.flip-card-data p{
  margin-bottom: 7px;
  font-size: 14px;
}
.flip-card-data{
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}

/* Text animations */
@keyframes showup {
  0% {opacity:0;}
  20% {opacity:0;width:50%;}
  80% {opacity:1;width:100%;}
  100% {opacity:1;width:100%;}
}

@keyframes slidein {
  0% { margin-left:-800px;opacity: 0; }
  20% { margin-left:-800px;opacity: 0; }
  45% { margin-left:0px; opacity: 1;}
  45% { margin-left:0px; opacity: 1;}
  100% { margin-left:0px; opacity: 0;}
}

@keyframes reveal {
  0% {opacity:0;width:0px;height: 0px;}
  20% {opacity:1;width:0px;height: 0px;}
  30% {width:155px;height: 155px;}
  80% {opacity:1;}
  100% {opacity:0;}
}

@media only screen and (max-width: 768px) {
  .nav-menu {
      position: fixed;
      left: -100%;
      top: 5rem;
      flex-direction: column;
      background-color: #fff;
      width: 100%;
      border-radius: 10px;
      text-align: center;
      transition: 0.3s;
      box-shadow:
          0 10px 27px rgba(0, 0, 0, 0.05);
  }

  .nav-menu.active {
      left: 0;
  }

  .nav-item {
      margin: 2.5rem 0;
  }

  .hamburger {
      display: block;
      cursor: pointer;
  }
  .hero-text {
    left: 12%;
  }
  .hero-text h1{
    font-size: 22px;
  }
  .hero-text h2{
    font-size: 14px;
  }
  .hero-text h3{
    font-size: 10px;
  }
  .portal-image{
    width: 125px;
    height: 125px;
    left: -50px;
  }
  .flip-card {
    background-color: transparent;
    width: 80%;
    height: 340px;
    perspective: 1000px;
    margin: 20px;
    border-radius: 8px;
  }

  @keyframes reveal {
    0% {opacity:0;width:0px;height: 0px;}
    20% {opacity:1;width:0px;height: 0px;}
    30% {width:125px;height: 125px;}
    80% {opacity:1;}
    100% {opacity:0;}
  }

}

/* Results styles */
.find-input{
  width: 300px;
  padding: 10px 8px;
  font-size: 18px;
  border-radius: 6px;
  border: solid 1px gray;
  border-right: none;
}

.button-find{
  width: 100px;
  height: 42px;
  border: solid 1px gray;
  border-radius: 6px;
  border-left: none;
  font-size: 17px;
  background-color: black;
  color: #7abc4b;
}
.characters-container h5{
  font-size: 40px;
  font-weight: 300;
  color: #bbb;
}

/* Footer styles */
.footer{
  width: 100%;
  min-height: 20vh;
  display: flex;
  justify-content: space-around;
  align-items: center;
  background-color: rgb(41, 41, 41);
  padding: 30px;
}
.footer-links{
  display: flex;
  flex-direction: column;
}
.footer-links p{
  font-size: 20px;
  margin-bottom: 12px;
  color: #4e8e42;
}

.footer-links a{
  font-size: 16px;
  margin-bottom: 10px;
  cursor: pointer;
  color: white;
}

.footer-links a:hover{
  color: #7abc4b;
}